<template>
  <transition name="toast-fade" @after-leave="afterLeave">
    <div v-show="toast" class="toast-wrapper" :style="{ backgroundColor: background || '' }">
      <template v-if="content">
        <div class="toast-content" :class="{'with-icon': icon}" v-html="content"></div>
      </template>
      <template v-else>
        <div class="toast-content" :class="{'with-icon': icon}" :style="toastCls">
          <span class="icon-def" :class="icon" v-if="icon"></span>
          {{message}}
        </div>
      </template>
    </div>
  </transition>
</template>

<script>
export default {
  data() {
    return {
      toast: false,
      message: "",
      icon: "", 
      background: null,
      toastBackground: null,
      textColor: null,
      textAlign: null,
      content: null,
      max: null
    };
  },
  computed: {
    toastCls() {
      let cls = {};

      if (this.toastBackground) {
        cls["background"] = this.toastBackground;
      }

      //如果是传入了content(html片段) 下面的不生效
      if (this.content) {
        return cls;
      }

      if (this.textColor) {
        cls["color"] = this.textColor;
      }

      if (this.textAlign) {
        cls["textAlign"] = this.textAlign;
      }

      if (this.max) {
        cls["maxWidth"] = `${this.max}px`;
      }

      return cls;
    }
  },
  methods: {
    afterLeave() {
      this.$emit("after");
    }
  }
};
</script>

<style lang="scss" scoped>
$toast-content-bg: rgba(68, 68, 68, 0.9);
$toast-border-radius: 16px;

.toast-wrapper {
  position: fixed;
  margin: 0;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 3000;
  user-select: none;
  transition: all linear 0.2s;
  opacity: 1;
}

.toast-wrapper .toast-content {
  display: inline-block;
  padding: 50px 30px;
  background: $toast-content-bg;
  color: #ffffff;
  border-radius: $toast-border-radius;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 3000;
  word-break: break-all;
  font-size: 28px;
  max-width: 450px;
  min-width: 360px;
  box-sizing: border-box;
  text-align: center; // 文本默认居中
  line-height: 40px;
  &.with-icon {
    padding: 40px 30px;
  }
  .icon-def{
    display:block;
    width: 100%;
    height: 60px;
    background-size: 60px 60px;
    background-position: center;
    background-repeat: no-repeat;
    margin-bottom: 16px;
    &.succ{
        background-image: url('data:image/png;base64,');
    }
    &.fail{
        background-image: url('data:image/png;base64,');
    }
  }
}

.toast-fade-enter,
.toast-fade-leave-to {
  opacity: 0;
}
</style>
